<template>
  <header>
    <h1 style="padding-top: 0.5rem" class="title $title-color has-text-white">
      {{ `Clips for ${title}` }}
    </h1>
    <div class="form">
      <p class="control has-icons-left">
        <input
          id="input"
          class="input is-normal"
          type="text"
          placeholder="Streamer"
          @input="inputHandler"
        />
        <span class="icon is-small is-left">
          <i class="fab fa-twitch"></i>
        </span>
      </p>
      <a
        href="#"
        style="margin-left: 0.5rem"
        class="button is-link"
        @click="getClips(inputValue)"
        >Find clips <i style="margin-left: 0.5rem" class="fas fa-search"></i>
      </a>
    </div>
  </header>
  <div class="container">
    <div class="clips" v-if="clip[0] != 1 && clip.length > 0">
      <div class="clip" v-for="value in clip" :key="value.id">
        <iframe
          :src="value.embed_url + `&parent=${url}`"
          height="400"
          width="500"
          allowfullscreen="true"
        >
        </iframe>
        <a
          class="button is-link"
          :href="value.thumbnail_url.replace(/-preview-480x272\.jpg/i, '.mp4')"
        >
          <i class="fas fa-download"></i>
        </a>
      </div>
    </div>
    <div
      class="loader"
      v-else-if="clip[0] == 1"
      style="margin-top: 10%; height: 100px; width: 100px"
    ></div>
    <h1 class="title has-text-white" v-else-if="clip.length == 0">
      no clips for this streamer
    </h1>
    <h1 class="title has-text-white" v-else>{{ JSON.parse(clip) }}</h1>
  </div>
  <link
    rel="stylesheet"
    href="https://cdn.jsdelivr.net/npm/bulma@0.9.3/css/bulma.min.css"
  />
</template>

<script>
import axios from "axios";

export default {
  name: "App",
  components: {},
  data() {
    return {
      clip: [1, 2, 3],
      errors: [],
      inputValue: "",
      title: "by_Owl",
      url: window.location.hostname
    };
  },
  created() {
    axios
      .get("http://localhost:5000/getClips")
      .then((response) => {
        this.clip = response.data;
        console.log(response);
      })
      .catch((error) => {
        this.errors.push(error);
      });
  },
  methods: {
    inputHandler(event) {
      this.inputValue = event.target.value;
    },
    getClips(streamer) {
      if (streamer == "") return;
      axios
        .get(`http://localhost:5000/getClips?streamer=${streamer}`)
        .then((response) => {
          this.clip = response.data;
          console.log(response);
          this.title = this.inputValue;
        })
        .catch((error) => {
          this.errors.push(error);
        });
    },
  },
};
</script>

