  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
.clips {
  display: grid;
  grid-template-columns: repeat(3, 600px);
  grid-template-rows: repeat(7, minmax(450px, 450px));
  margin: 0 auto;
  margin-top: 1rem;
}
.clip iframe {
  width: 100%;
  height: 85%;
  box-shadow: 12px 12px 2px 1px rgba(0, 0, 255, 0.2);
}
.clip {
  display: inline-block;
  margin: 10px;
  top: 0;
  left: 0;
}
input {
  margin-right: 5px;
}
.form {
  display: flex;
  width: 400px;
  margin: 0 auto;
}
